import axios from 'axios';


import {
CONTRIBUTORS_LOADED,
CONTRIBUTORS_LOADING
} from './types';




// GET A CAMPAIGNS FOR A PARTICULAR USER
export const getContributors = (id) => dispatch => {
    dispatch({
      type:  CONTRIBUTORS_LOADING
    });
  
    axios
      .get(`/api/payout/${id}`)
      .then(res =>{

        dispatch({
          type: CONTRIBUTORS_LOADED,
          payload: res.data
        })
        // console.log(res.data)
      }
      )
      .catch(err => {
        // dispatch(
        //   returnErrors(err.response.data, err.response.status, "REGISTER_FAIL")
        // );
        console.log(err);
        // dispatch({
        //   type: CREATE_CAMPAIGN_FAILED
        // });
      });
  };
